@import "base";

@import "mixins/transition";
@import "mixins/slider";

body {
  background: var(--color-bg-primary);
}

#solid-viewer {
  cursor: pointer;
}

#solid-viewer:active {
  cursor: move;
}

.slider {
  display: none; /* Don't display by default, let JS handle this */
  width: 325px;
  border: 1px #111 solid;
  border-radius: 4px;
  margin: 10px auto;
  position: relative;
  .slider-track {
    width: 100%;
  }
  .dragger {
    left: 315px;
  }
}
