/* Styles for the slider bar */
.slider {
  z-index: 999;
  background-color: rgba(100, 100, 100, 0.8);
  height: 25px;
  width: 100%;
  border-top: 1px #111 solid;
  margin: 10px auto;

  .slider-track {
    position: static;
    width: 100%;
    height: 0px;
    margin: 12px auto;
    border-top: #fff 1px solid;
  }

  .dragger {
    text-shadow: none;
    position: absolute;
    top: 6.5px;
    left: 25%;
  }

  .dragger:hover, .dragger:active {
    cursor: pointer;
    color: #eee;
  }
}
